import React, { useState, useContext, useEffect } from 'react';
import { useFilePicker } from 'use-file-picker';
import { AppContext } from '../App';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import { Button } from '@mui/material';
import CssBaseline from '@mui/material/CssBaseline';
import load from './img/loading-loading-forever.gif';

const darkTheme = createTheme({
    palette: {
        mode: 'dark',
    },
});
export default function ImgUploader() {
    const { inputFields, setInputFields } = useContext(AppContext);

    const handleInputChange = (images) => {
        const newImages = images.filesContent.map((fileContent) => fileContent.content);
        setInputFields((prevState) => ({ ...prevState, pictures: prevState.pictures.concat(newImages) }));
        setTimeout(() => {
            console.log(inputFields);
        }, 1000);
    };

    const handleImageRemove = (indexToRemove) => {
        setInputFields((prevState) => {
            const updatedPictures = prevState.pictures.filter((_, index) => index !== indexToRemove);
            return { ...prevState, pictures: updatedPictures };
        });
    };

    const [openFileSelector, { filesContent, loading, errors }] = useFilePicker({
        readAs: 'DataURL',
        accept: 'image/*',
        multiple: true,
        limitFilesConfig: { max: 2 },
        maxFileSize: 50,
        imageSizeRestrictions: {
            maxHeight: 3000, // in pixels
            maxWidth: 3000,
            minHeight: 100,
            minWidth: 100,
        },
        onFilesSuccessfulySelected: (filesContent) => handleInputChange(filesContent),
    });

    if (loading) {
        return (
            <ThemeProvider theme={darkTheme}>
                <CssBaseline />
                <center>
                    <img src={load} height={200} alt='loading' />
                </center>
            </ThemeProvider>
        );
    }

    if (errors.length) {
        return <div>{errors}</div>;
    }

    return (
        <ThemeProvider theme={darkTheme}>
            <CssBaseline />
            <br />
            <Button
                onClick={() => openFileSelector()}
                variant='contained'
                component='span'
                fullWidth={true}
            >
                ДОБАВИТЬ ИЗОБРАЖЕНИЕ
            </Button>
            <br />

            {inputFields.pictures && inputFields.pictures.map((imageSrc, index) => (
                <center key={index}>
                    <br />
                    <img src={imageSrc} alt={`Map ${index + 1}`} height={100} />
                    <br />
                    <Button
                        onClick={() => handleImageRemove(index)}
                        variant='outlined'
                        color='error'
                        size='medium'
                    >
                        Удалить
                    </Button>
                    <br />
                </center>
            ))}
        </ThemeProvider>
    );
}