import React, {useContext} from 'react';
import {ThemeProvider, createTheme} from "@mui/material/styles";
import CssBaseline from "@mui/material/CssBaseline";
import {
    Button,
    FormControl,
    FormControlLabel,
    RadioGroup,
    Radio,
    TextField, Accordion, AccordionSummary, Typography, AccordionDetails, Grid, Box,
} from "@mui/material";
import {AdapterDayjs} from '@mui/x-date-pickers/AdapterDayjs';
import {LocalizationProvider} from '@mui/x-date-pickers/LocalizationProvider';
import {DatePicker} from '@mui/x-date-pickers/DatePicker';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import {useState} from "react";
import 'dayjs/locale/ru';
import dayjs from "dayjs";
import {AppContext} from "../App";
import axios from "axios";

const darkTheme = createTheme({
    palette: {
        mode: "dark",
    }
});

function Input() {
    const {inputFields, setInputFields, setImage1} = useContext(AppContext);
//добавление в контекст
    const handleInputChange = (e) => {
        const {id, value} = e.target;
        setInputFields((prevState) => ({
            ...prevState,
            [id]: value
        }));
    };
//добавление даты в контекст
    const handleDateOneChange = (date) => {
        setInputFields(prevState => ({
            ...prevState,
            dateOne: date.$d
        }));
    };
    const handleDateTwoChange = (date) => {
        setInputFields(prevState => ({
            ...prevState,
            dateTwo: date.$d
        }));
    };
//состояние монтажа
    const [mountOrNot, setMountOrNot] = useState('');
    const handleMountOrNotChange = (mnt) => {
        setMountOrNot(mnt.target.value);
    };
//Запрос координат и карты по ним
    const getCoords = () => {
        const addressToAPI = inputFields.address;
        axios
            .get('https://geocode-maps.yandex.ru/1.x/', {
                params: {
                    geocode: addressToAPI,
                    apikey: '55c8816a-1c05-42cd-8e95-1066282e5eaf',
                    format: 'json',
                },
            })
            .then((response) => {
                const coords = response.data.response.GeoObjectCollection.featureMember[0].GeoObject.Point.pos.split(" ");
                const mapImageUrl = `https://static.maps.2gis.com/1.0?zoom=14&amp;size=600,350&amp;markers=` + coords[0] + `,` + coords[1];
                setInputFields(prevState => ({
                    ...prevState,
                    mapImage: mapImageUrl
                }));
            })
            .catch((error) => {
                console.error('Error getting coordinates:', error);
            });
    };

    return (
        <ThemeProvider theme={darkTheme}>
            <CssBaseline/>
            <br/>
            <FormControl fullWidth>
                {/* тут основные данные*/}
                <TextField id="number" label="Номер" variant="outlined" value={inputFields.number}
                           onChange={handleInputChange}/><br/>
                <TextField id="manager" label="Менеджер" variant="outlined" value={inputFields.manager}
                           onChange={handleInputChange}/><br/>
                <TextField id="costumer" label="Заказчик" variant="outlined" value={inputFields.costumer}
                           onChange={handleInputChange}/><br/>
                <TextField id="contact" label="Контакты" variant="outlined" value={inputFields.contact}
                           onChange={handleInputChange}/><br/>
                <TextField id="folder" label="Путь к папке" variant="outlined" value={inputFields.folder}
                           onChange={handleInputChange}/><br/>
                {/*добавление даты*/}
                <LocalizationProvider dateAdapter={AdapterDayjs} adapterLocale="ru">
                    <DatePicker
                        label="Дата составления заявки"
                        id="dateOne"
                        value={dayjs(inputFields.dateOne)}
                        onChange={handleDateOneChange}
                        renderInput={(params) => <TextField {...params} id="dateOne-input"/>}
                    />
                </LocalizationProvider><br/>
                {/*добавление даты дедлайна*/}
                <LocalizationProvider dateAdapter={AdapterDayjs} adapterLocale="ru">
                    <DatePicker
                        label="Дедлайн"
                        id="dateTwo"
                        value={dayjs(inputFields.dateTwo)}
                        onChange={handleDateTwoChange}
                        renderInput={(params) => <TextField {...params} id="dateTwo-input"/>}
                    />
                </LocalizationProvider><br/>
                {/*Тут выбираем цвет лица*/}
                <Accordion>
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                        id="panel1a-header"
                    >
                        <Typography>Цвет лицевой поверхности</Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                        <RadioGroup
                            aria-labelledby="type-label"
                            row
                            value={inputFields.colorFace}
                            onChange={handleInputChange}>
                            <FormControlLabel
                                value="#000000"
                                control={<Radio id='colorFace'/>}
                                checked={inputFields.colorFace == "#000000"}
                                label="Черный"/>
                            <FormControlLabel
                                value="#DDDDDD"
                                control={<Radio id='colorFace'/>}
                                checked={inputFields.colorFace == "#DDDDDD"}
                                label="Белый"/>
                            <FormControlLabel
                                value="#FF0000"
                                control={<Radio id='colorFace'/>}
                                checked={inputFields.colorFace == "#FF0000"}
                                label="Красный"/>
                            <FormControlLabel
                                value="#008000"
                                control={<Radio id='colorFace'/>}
                                checked={inputFields.colorFace == "#008000"}
                                label="Зеленый"/>
                            <FormControlLabel
                                value="#00008f"
                                control={<Radio id='colorFace'/>}
                                checked={inputFields.colorFace == "#00008f"}
                                label="Синий"
                            />
                            <FormControlLabel
                                value="another"
                                control={<Radio id='colorFace'/>}
                                checked={inputFields.colorFace == "another"}
                                disabled={true}
                                label="Другой"
                            />
                        </RadioGroup>
                    </AccordionDetails>
                </Accordion>
                {/*Тут выбираем цвет борта*/}
                <Accordion>
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                        id="panel1a-header"
                    >
                        <Typography>Цвет борта</Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                        <RadioGroup
                            aria-labelledby="type-label"
                            row
                            value={inputFields.colorBoard}
                            onChange={handleInputChange}>
                            <FormControlLabel
                                value="#000000"
                                control={<Radio id='colorBoard'/>}
                                checked={inputFields.colorBoard == "#000000"}
                                label="Черный"/>
                            <FormControlLabel
                                value="#DDDDDD"
                                control={<Radio id='colorBoard'/>}
                                checked={inputFields.colorBoard == "#DDDDDD"}
                                label="Белый"/>
                            <FormControlLabel
                                value="#FF0000"
                                control={<Radio id='colorBoard'/>}
                                checked={inputFields.colorBoard == "#FF0000"}
                                label="Красный"/>
                            <FormControlLabel
                                value="#008000"
                                control={<Radio id='colorBoard'/>}
                                checked={inputFields.colorBoard == "#008000"}
                                label="Зеленый"/>
                            <FormControlLabel
                                value="#00008f"
                                control={<Radio id='colorBoard'/>}
                                checked={inputFields.colorBoard == "#00008f"}
                                label="Синий"
                            />
                            <FormControlLabel
                                value="another"
                                control={<Radio id='colorBoard'/>}
                                checked={inputFields.colorBoard == "another"}
                                disabled={true}
                                label="Другой"
                            />
                        </RadioGroup>
                    </AccordionDetails>
                </Accordion>
                {/*Ширина борта*/}
                <Accordion>
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                        id="panel1a-header"
                    >
                        <Typography>Ширина борта</Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                        <RadioGroup
                            aria-labelledby="type-label"
                            row
                            value={inputFields.heightBoard}
                            onChange={handleInputChange}>
                            <FormControlLabel
                                value="20 мм"
                                control={<Radio id='heightBoard'/>}
                                checked={inputFields.heightBoard == "20 мм"}
                                label="20 мм"/>
                            <FormControlLabel
                                value="40 мм"
                                control={<Radio id='heightBoard'/>}
                                checked={inputFields.heightBoard == "40 мм"}
                                label="40 мм"/>
                            <FormControlLabel
                                value="60 мм"
                                control={<Radio id='heightBoard'/>}
                                checked={inputFields.heightBoard == "60 мм"}
                                label="60 мм"/>
                            <FormControlLabel
                                value="80 мм"
                                control={<Radio id='heightBoard'/>}
                                checked={inputFields.heightBoard == "80 мм"}
                                label="80 мм"/>
                            <FormControlLabel
                                value="100 мм"
                                control={<Radio id='heightBoard'/>}
                                checked={inputFields.heightBoard == "100 мм"}
                                label="100 мм"
                            />
                            <FormControlLabel
                                value="another"
                                control={<Radio id='colorBoard'/>}
                                checked={inputFields.heightBoard == "another"}
                                disabled={true}
                                label="Другой"
                            />
                        </RadioGroup>
                    </AccordionDetails>
                </Accordion>
                {/*Тут выбираем цвет рамы*/}
                <Accordion>
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                        id="panel1a-header"
                    >
                        <Typography>Цвет рамы</Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                        <RadioGroup
                            aria-labelledby="type-label"
                            row
                            value={inputFields.colorFrame}
                            onChange={handleInputChange}>
                            <FormControlLabel
                                value="#000000"
                                control={<Radio id='colorFrame'/>}
                                checked={inputFields.colorFrame == "#000000"}
                                label="Черный"/>
                            <FormControlLabel
                                value="#DDDDDD"
                                control={<Radio id='colorFrame'/>}
                                checked={inputFields.colorFrame == "#DDDDDD"}
                                label="Белый"/>
                            <FormControlLabel
                                value="#FF0000"
                                control={<Radio id='colorFrame'/>}
                                checked={inputFields.colorFrame == "#FF0000"}
                                label="Красный"/>
                            <FormControlLabel
                                value="#008000"
                                control={<Radio id='colorFrame'/>}
                                checked={inputFields.colorFrame == "#008000"}
                                label="Зеленый"/>
                            <FormControlLabel
                                value="#00008f"
                                control={<Radio id='colorFrame'/>}
                                checked={inputFields.colorFrame == "#00008f"}
                                label="Синий"
                            />
                            <FormControlLabel
                                value="another"
                                control={<Radio id='colorFrame'/>}
                                checked={inputFields.colorFrame == "another"}
                                disabled={true}
                                label="Другой"
                            />
                        </RadioGroup>
                    </AccordionDetails>
                </Accordion><br/>

                {/*Монтаж или что вообще*/}
                <form onChange={handleInputChange} >
                        <RadioGroup
                            aria-labelledby="type-label"
                            row
                            defaultValue="mount"

                            onChange={handleMountOrNotChange}>
                            <FormControlLabel
                                value="mount"
                                control={<Radio id='mountOrNot'/>}
                                checked={inputFields.mountOrNot == "mount"}
                                label="Монтаж"/>
                            <FormControlLabel
                                value="send"
                                control={<Radio id='mountOrNot'/>}
                                checked={inputFields.mountOrNot == "send"}
                                label="Отправка"/>
                            <FormControlLabel
                                value="self"
                                control={<Radio id='mountOrNot'/>}
                                checked={inputFields.mountOrNot == "self"}
                                label="Самовывоз"
                            />
                        </RadioGroup>
                    </form>

                {inputFields.mountOrNot == "mount" ? (
                    <div>
                        <TextField id="address" label="Монтаж по адресу" variant="outlined" value={inputFields.address}
                                   onChange={handleInputChange} fullWidth={true}/>
                        <br/>
                        <Button variant="contained" component="span" fullWidth={true} onClick={getCoords}>
                            ПОКАЗАТЬ КАРТУ
                        </Button>
                        <br/>
                        <div>
                            <center>{inputFields.mapImage && <img src={inputFields.mapImage} alt="Map"/>}</center>
                        </div>
                    </div>
                ) : inputFields.mountOrNot == "send" ? (
                    <TextField id="address" label="Отправить по адресу" variant="outlined" value={inputFields.address}
                               onChange={handleInputChange}/>
                ) : inputFields.mountOrNot == "self" ? (
                    "Заказчик заберет сам"
                ) : (
                    "Выберите монтаж / отправка / самовывоз"
                )}
                <br/><br/><br/><br/><br/>
            </FormControl>
        </ThemeProvider>
    );
}

export default Input;
