import React, {useContext} from 'react';
import './styles.css'
import pdfMake from "pdfmake/build/pdfmake";
import pdfFonts from "pdfmake/build/vfs_fonts";
import { useEffect, useRef, useState } from "react";
import {AppContext} from "../App";
import CssBaseline from "@mui/material/CssBaseline";
import {createTheme, ThemeProvider} from "@mui/material/styles";
import {Button} from "@mui/material";

pdfMake.vfs = pdfFonts.pdfMake.vfs;

pdfMake.fonts = {
    NimbusSans: {
        normal: "NimbusSanL-Reg.otf",
        bold: "NimbusSanL-Bol.otf",
        italics: "NimbusSanL-RegIta.otf",
        bolditalics: "NimbusSanL-BolIta.otf"
    },
    Montserrat: {
        normal: "Montserrat-Regular.ttf",
        bold: "Montserrat-Bold.ttf",
        italics: "Montserrat-Italic.ttf"
    }
};

export default function Exporter() {
    const [url, setUrl] = useState(null);
    const dataFromContext = useContext(AppContext);
    const [blob64, setBlob64] = useState("");
    const today = new Date();
    const month = today.getMonth() + 1;
    const formattedDate = `${today.getDate()}.${month<10?'0'+month:month}.${today.getFullYear()}`;

    const darkTheme = createTheme({
        palette: {
            mode: 'dark',
        },
    });

    //Map to blob
    useEffect(() => {
        const fetchData = async () => {
            try {
                const response = await fetch(dataFromContext.inputFields.mapImage);
                const blob = await response.blob();
                const base64data = await blobToBase64(blob);
                setBlob64(base64data);
            } catch (error) {
                console.log(error);
            }
        };
        console.log(dataFromContext.inputFields.mapImage)
        fetchData();
    }, []);

    const blobToBase64 = async (blob) => {
        return new Promise((resolve) => {
            const reader = new FileReader();
            reader.onloadend = () => {
                const base64data = reader.result;
                resolve(base64data);
            };
            reader.readAsDataURL(blob);
        });
    };

    console.log("blob64:", blob64);

    const dataTyper = (data) => {
        return data.slice(0, 14);
    }

    //Осветлительно-затемняющий функционал
    function LightenDarkenColor(col, amt) {
        var usePound = false;
        if (col[0] == "#") {
            col = col.slice(1);
            usePound = true;
        }
        let num = parseInt(col, 16);
        let r = (num >> 16) + amt;
        if (r > 255) r = 255;
        else if  (r < 0) r = 0;
        let b = ((num >> 8) & 0x00FF) + amt;
        if (b > 255) b = 255;
        else if  (b < 0) b = 0;
        let g = (num & 0x0000FF) + amt;
        if (g > 255) g = 255;
        else if (g < 0) g = 0;
        return (usePound?"#":"") + (g | (b << 8) | (r << 16)).toString(16);
    }

    let colorBoard = dataFromContext.inputFields.colorBoard ? dataFromContext.inputFields.colorBoard : '#000000';
    let colorBoardLight = LightenDarkenColor(colorBoard, 70);
    let colorFrame = dataFromContext.inputFields.colorFrame ? dataFromContext.inputFields.colorFrame : '#555555';
    let colorFrameLight = LightenDarkenColor(colorFrame, 70);
    let colorFrameDark = LightenDarkenColor(colorFrame, -70);
    let colorFace = dataFromContext.inputFields.colorFace ? dataFromContext.inputFields.colorFace : '#FFFFFF';

    const docDefinition = {
        pageSize: "A4",
        pageOrientation: "portrait",
        pageMargins: [40, 20, 40, 60],
        content: [
            //Логотип
            {
                svg: '<svg xmlns="http://www.w3.org/2000/svg" width="150" height="26" viewBox="0 0 150 26" fill="none">\n' +
                    '<path d="M11.8516 26H0.267578V0.300781H11.8516V4.43164H5.04883V10.5488H11.3594V14.6973H5.04883V21.8164H11.8516V26Z" fill="#0038E6"/>\n' +
                    '<path d="M30.3613 26H25.3867L21.7129 16.7891L18.3027 26H13.3281L19.1992 12.6406L13.7676 0.300781H18.584L21.9766 8.75586L24.9121 0.300781H29.9219L24.4902 12.8516L30.3613 26Z" fill="#0038E6"/>\n' +
                    '<path d="M40.5391 26H35.7754V4.50195H30.8535V0.300781H45.4609V4.50195H40.5391V26Z" fill="#0038E6"/>\n' +
                    '<path d="M59.8047 26H48.2207V0.300781H59.8047V4.43164H53.002V10.5488H59.3125V14.6973H53.002V21.8164H59.8047V26Z" fill="#0038E6"/>\n' +
                    '<path d="M68.3301 15.998V26H63.5488V0.300781H69.8066C72.4551 0.300781 74.4062 0.927734 75.6602 2.18164C76.9258 3.43555 77.5586 5.35156 77.5586 7.92969C77.5586 11.1758 76.4629 13.4434 74.2715 14.7324L79.2109 26H74.0957L70.0352 15.998H68.3301ZM68.3301 11.8848H69.5605C70.6855 11.8848 71.4941 11.5684 71.9863 10.9355C72.4785 10.291 72.7246 9.33594 72.7246 8.07031C72.7246 6.75781 72.4609 5.82617 71.9336 5.27539C71.418 4.71289 70.6152 4.43164 69.5254 4.43164H68.3301V11.8848Z" fill="#0038E6"/>\n' +
                    '<path d="M99.5488 26H93.7129L85.7676 7.38477H85.5391C85.832 8.80273 85.9785 10.3262 85.9785 11.9551V26H81.5488V0.300781H87.4199L95.2949 18.7227H95.4883C95.2656 17.4922 95.1543 16.0273 95.1543 14.3281V0.300781H99.5488V26Z" fill="#0038E6"/>\n' +
                    '<path d="M116.248 26L114.771 19.5137H108.074L106.598 26H101.816L108.18 0.195312H114.666L121.047 26H116.248ZM113.875 15.2773L112.504 9.30078C112.422 8.9375 112.234 8.0293 111.941 6.57617C111.66 5.11133 111.49 4.13281 111.432 3.64062C111.279 4.64844 111.086 5.76172 110.852 6.98047C110.617 8.1875 110.008 10.9531 109.023 15.2773H113.875Z" fill="#0038E6"/>\n' +
                    '<path d="M123.314 26V0.300781H128.096V21.8164H134.723V26H123.314Z" fill="#0038E6"/>\n' +
                    '<path d="M137.799 26V0.300781H142.58V21.8164H149.207V26H137.799Z" fill="#0038E6"/>\n' +
                    '</svg>', alignment: 'left'
            },
            {text: '\n'},
            //Блок с основной информацией и картой
            {
                //alignment: 'justify',
                columnGap: 0,
                columns: [
                    {
                        //Номер
                        text:
                        [dataFromContext.inputFields.number && {text:
                                [`Номер: `, {text: `${dataFromContext.inputFields.number}\n`, bold: true}]},
                            //Менеджер
                            dataFromContext.inputFields.manager && {text:
                                    [`Менеджер: `, {text: `${dataFromContext.inputFields.manager}\n`, bold: true}]},
                        //Заказчик
                        dataFromContext.inputFields.costumer && {text:
                                [`Заказчик: `, {text: `${dataFromContext.inputFields.costumer}\n`, bold: true}]},
                        //Контакт
                        dataFromContext.inputFields.contact && {text:
                                [`Контакт: `, {text: `${dataFromContext.inputFields.contact}\n`, bold: true}]},
                            //Путь к папке
                            dataFromContext.inputFields.folder && {text:
                                    [`Путь к папке: `, {text: `${dataFromContext.inputFields.folder}\n`, bold: true}]},
                        //Дата составления заявки
                            dataFromContext.inputFields.dateOne
                            ? {text: [`Дата составления заявки: `, {text: `${dataFromContext.inputFields.dateOne.toLocaleDateString()}`, bold: true}]}
                            : {text: [`Дата составления заявки: `, {text: `${formattedDate}`, bold: true}]},'\n',
                        //Дэдлайн
                            dataFromContext.inputFields.dateTwo
                            ? {text: [`Дэдлайн: `, {text: `${dataFromContext.inputFields.dateTwo.toLocaleDateString()}`, bold: true}]}
                            : {text: [`Дэдлайн: `, {text: `не определен`, bold: true}]},'\n',
                        //Адрес
                        dataFromContext.inputFields.address && {text:
                                [`Адрес: `, {text: `${dataFromContext.inputFields.address}`, bold: true}]}]
                    },
                        //Карта
                        dataTyper(blob64) == "data:text/html" ? '' : {image: blob64, width: 300 , alignment: 'center'},
                ]
            },
            //Разделитель
            {
                svg: '<svg width="515" height="3" xmlns="http://www.w3.org/2000/svg">\n' +
                '<rect width="515" height="3" fill="#0038E6"/>\n' +
                '</svg>\n', margin: [0,0,0,0]
            },
            '\n',
            //Блок описания вывески
            {
                alignment: 'justify',
                columns: [
                    //Превью буквы
                    {
                        svg: '<svg xmlns="http://www.w3.org/2000/svg" xml:space="preserve" width="150px" height="150px" version="1.1" style="shape-rendering:geometricPrecision; text-rendering:geometricPrecision; image-rendering:optimizeQuality; fill-rule:evenodd; clip-rule:evenodd" viewBox="0 0 303.36 303.36" xmlns:xlink="http://www.w3.org/1999/xlink" xmlns:xodm="http://www.corel.com/coreldraw/odm/2003">\n'+
                            '<defs>\n'+
                            '<style type="text/css">\n'+
                            '<![CDATA[\n'+
                            '.str0 {stroke:#2B2A29;stroke-width:0.76;stroke-linecap:round;stroke-linejoin:round;stroke-miterlimit:22.9256}\n'+
                            ']]>\n'+
                            '</style>\n'+
                            '</defs>\n'+
                            '<g id="Layer_x0020_1">\n'+
                            '<metadata id="CorelCorpID_0Corel-Layer"/>\n'+
                            '<g id="_2375251128288">\n'+
                            `<polygon fill='${colorFrameLight}' class="str0" points="0.38,250.18 296.34,213.52 302.98,217.64 7.02,254.3 "/>\n`+
                            `<polygon fill='${colorFrameDark}' class="str0" points="0.38,265.09 7.02,264.27 7.02,269.21 "/>\n`+
                            `<polygon fill='${colorFrameDark}' class="str0" points="0.38,250.18 7.02,254.3 7.02,264.27 0.38,265.09 "/>\n`+
                            `<polygon fill='${colorFrame}' class="str0" points="7.02,254.3 302.98,217.64 302.98,232.55 7.02,269.21 "/>\n`+
                            `<polygon fill='${colorFrame}' class="str0" points="7.02,59.55 302.98,22.89 302.98,37.8 7.02,74.47 "/>\n`+
                            `<polygon fill='${colorFrameLight}' class="str0" points="0.38,55.43 296.34,18.77 302.98,22.89 7.02,59.55 "/>\n`+
                            `<polygon fill='${colorFrameDark}' class="str0" points="0.38,70.34 7.02,69.52 7.02,74.47 "/>\n`+
                            `<polygon fill='${colorFrameDark}' class="str0" points="0.38,55.43 7.02,59.55 7.02,69.52 0.38,70.34 "/>\n`+
                            `<polygon fill='${colorBoard}' class="str0" points="213.19,214.9 190.74,198.22 212.38,261.15 235.27,279.12 "/>\n`+
                            `<polygon fill='${colorBoardLight}' class="str0" points="208.28,13.02 185.93,0.38 104.98,10.41 125.67,23.25 "/>\n`+
                            `<polygon fill='${colorBoard}' class="str0" points="125.67,23.25 104.98,10.41 23.62,284.54 42.66,302.98 "/>\n`+
                            `<polygon fill='${colorBoard}' class="str0" points="167.38,64.88 145.85,51.2 178.32,156.27 200.51,172.1 "/>\n`+
                            `<polygon fill='${colorBoardLight}' class="str0" points="200.51,172.1 178.32,156.27 114.19,164.22 135.08,180.2 "/>\n`+
                            `<path fill='${colorFace}' class="str0" d="M101.95 295.63l20.86 -69.54 90.37 -11.19 22.09 64.22 58.07 -7.19 -85.06 -258.91 -82.61 10.23 -83.01 279.73 59.29 -7.35zm65.43 -230.75l33.13 107.22 -65.43 8.1 32.3 -115.32z"/>\n`+
                            '</g>\n'+
                            '</g>\n'+
                            '</svg>',
                        alignment: 'center'
                    },
                    //Характеристики вывески
                    {
                        text:
                                [
                                //Цвет лица
                                dataFromContext.inputFields.colorFace && {text:
                                    [`Цвет лица: `, {text: `${dataFromContext.inputFields.colorFace}`, bold: true}]},'\n',
                                //Ширина борта
                                dataFromContext.inputFields.heightBoard && {text:
                                    [`Ширина борта: `, {text: `${dataFromContext.inputFields.heightBoard}`, bold: true}]},'\n',
                                //Цвет борта
                                dataFromContext.inputFields.colorBoard && {text:
                                     [`Цвет борта: `, {text: `${dataFromContext.inputFields.colorBoard}`, bold: true}]},'\n',
                                //Цвет рамы
                                dataFromContext.inputFields.colorFrame && {text:
                                      [`Цвет рамы: `, {text: `${dataFromContext.inputFields.colorFrame}`, bold: true}]},'\n',
                                ]
                    }
                ]
            },
            '\n',
            //картника с привязкой или визуализацией
            {text: `Габариты, визуализация:`, bold: true},
            //dataFromContext.inputFields.pictures && {image:
                //dataFromContext.inputFields.pictures, width: 350 , alignment: 'center'},
            dataFromContext.inputFields.pictures && dataFromContext.inputFields.pictures.map((imageSrc, index) => (
                {image: imageSrc, width: 350 , alignment: 'center'}
                ))
        ],
        defaultStyle: {
            font: "Montserrat",
            fontSize: 10,
        }
    };

    useEffect(() => {
        return () => {
            if (url !== null) {
                URL.revokeObjectURL(url);
            }
        };
    }, [url]);

    const create = () => {
        const pdfDocGenerator = pdfMake.createPdf(docDefinition);
        pdfDocGenerator.getBlob((blob) => {
            const url = URL.createObjectURL(blob);
            setUrl(url);
        });
    };

    const download = () => {
        const pdfDocGenerator = pdfMake.createPdf(docDefinition);
        pdfDocGenerator.download();
    };

    return (
        <ThemeProvider theme={darkTheme}>
            <CssBaseline />
            <br />
            <Button
                onClick={create}
                variant='contained'
                component='span'
                fullWidth={true}
            >
                ПРЕДПРОСМОТР
            </Button>
            <br/>
            <Button
                onClick={download}
                variant='contained'
                component='span'
                fullWidth={true}
            >
                СКАЧАТЬ
            </Button>
            <br/>
        <div className="App">
            {url && (
                <div>
                    <object
                        style={{
                            width: "100%",
                            height: "100vh"
                        }}
                        data={url}
                        type="application/pdf"
                    >
                        <embed src={url} type="application/pdf" />
                    </object>
                </div>
            )}
        </div>
        </ThemeProvider>
    );
}

// Hook
function usePrevious(value) {
    // The ref object is a generic container whose current property is mutable ...
    // ... and can hold any value, similar to an instance property on a class
    const ref = useRef();

    // Store current value in ref
    useEffect(() => {
        ref.current = value;
    }, [value]); // Only re-run if value changes

    // Return previous value (happens before update in useEffect above)
    return ref.current;
}
